import { g as getYear, c as committeeLoad, a as committeeActions } from "../../../../../chunks/committee.server.js";
const load = async ({ locals, url }) => {
  const { prisma } = locals;
  const year = getYear(url);
  const phadderGroups = prisma.phadderGroup.findMany({
    where: {
      year
    },
    include: {
      nollor: true,
      phaddrar: {
        include: {
          member: true
        }
      }
    },
    orderBy: {
      createdAt: "asc"
    }
  });
  return committeeLoad(prisma, "nollu", url).then(async (data) => ({
    ...data,
    phadderGroups: await phadderGroups
  }));
};
const actions = committeeActions("nollu");
export {
  actions,
  load
};
